* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  list-style: none;
    padding: 0;
}
img{
  display: flex;
  width: 70px;
  height: 50px;
  margin-right: 15px;
}
li{
  display: flex;
  align-items:center;
  margin-bottom: 20px;
}
.title{
  display: flex;
  font-size: 30px;
  font-weight: 700;
}